.product-screen {
    max-width: 1300px;
    margin: 1rem auto;
    display: flex;
}

.product-screen-left {
    display: flex;
    flex: 0.8;
}

.left-image {
    margin: 1rem;
    flex: 0.6;
}

.left-info {
    margin: 1rem;
    flex: 0.4;
    background: #fff;
    height: fit-content;
    font-size: 0.9rem;
}

.left-name {
    font-weight: bold;
    font-size: 1.3rem;
}

.left-info>p, .right-info>p {
    padding: 1rem;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.left-info>p:last-child, .right-info>p:last-child {
    padding: 1rem;
    border: none;
}

.product-screen-right {
    flex: 0.2;
}

.right-info {
    width: 250px;
    margin: 1rem;
    background: #fff;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
}

.right-info p {
    padding: 1rem;
    font-size: 0.8rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.right-button>Button {
    grid-column: 1/-1;
}

.right-quantity>input {
    width: 85%;
    height: 25px;
    text-align: end;
}

@media (max-width: 960px) {
    .product-screen {
        flex-direction: column;
    }
    .product-screen-left {
        flex-direction: column;
        flex: 1;
    }
    .left-image {
        flex: 1;
    }
    .left-info {
        flex: 1;
    }
    .product-screen-right {
        flex: 1;
        padding: 1rem;
    }
    .right-info {
        width: 100%;
        margin: 0;
    }
}